body {
  background-color: #282c34;
  color: white;
}

input,
select,
button {
  cursor: pointer;
  font-size: 18px;
}

label {
  cursor: pointer;
}

fieldset {
  margin: 12px;
}

.App {
  max-height: 100vh;
  padding: 30px;
}

.App-header {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 1vmin);
}

.App-header>span {
  font-size: 14px;
}

.App-header>span>a {
  display: inline-block;
  color: lightseagreen;
  text-decoration: none;
}

.App-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-body>div,
.App-body>form {
  margin-bottom: 30px;
}

.inline_block {
  display: inline-block;
}

.buttons {
  text-align: center;
}

.buttons>button {
  margin: 15px;
}

.results-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.result-container {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 15px;
}

.result-player {
  font-size: 14px;
}

@media only screen and (max-width: 777px) {
  body {
    text-align: center;
  }

  fieldset>div,
  fieldset>select,
  fieldset>input {
    text-align: left;
  }
}